import { VNodeFlags, ChildrenFlags } from './flags'

export const Fragment = Symbol()
export const Portal = Symbol()

export function h(tag, data = null, children = null) {
  // ç¡®å®š flags
  let flags = null
  if (typeof tag === 'string') {
    flags = tag === 'svg' ? VNodeFlags.ELEMENT_SVG : VNodeFlags.ELEMENT_HTML
  } else if (tag === Fragment) {
    flags = VNodeFlags.FRAGMENT
  } else if (tag === Portal) {
    flags = VNodeFlags.PORTAL
    tag = data && data.target
  } else {
    // å…¼å®¹ Vue2 çš„å¯¹è±¡å¼ç»„ä»¶
    if (tag !== null && typeof tag === 'object') {
      flags = tag.functional
        ? VNodeFlags.COMPONENT_FUNCTIONAL // å‡½æ•°å¼ç»„ä»¶
        : VNodeFlags.COMPONENT_STATEFUL_NORMAL // æœ‰çŠ¶æ€ç»„ä»¶
    } else if (typeof tag === 'function') {
      // Vue3 çš„ç±»ç»„ä»¶
      flags =
        tag.prototype && tag.prototype.render
          ? VNodeFlags.COMPONENT_STATEFUL_NORMAL // æœ‰çŠ¶æ€ç»„ä»¶
          : VNodeFlags.COMPONENT_FUNCTIONAL // å‡½æ•°å¼ç»„ä»¶
    }
  }

  // ç¡®å®š childFlags
  let childFlags = null
  if (Array.isArray(children)) {
    const { length } = children
    if (length === 0) {
      // æ²¡æœ‰ children
      childFlags = ChildrenFlags.NO_CHILDREN
    } else if (length === 1) {
      // å•ä¸ªå­èŠ‚ç‚¹
      childFlags = ChildrenFlags.SINGLE_VNODE
      children = children[0]
    } else {
      // å¤šä¸ªå­èŠ‚ç‚¹ï¼Œä¸”å­èŠ‚ç‚¹ä½¿ç”¨key
      childFlags = ChildrenFlags.KEYED_VNODES
      children = normalizeVNodes(children)
    }
  } else if (children == null) {
    // æ²¡æœ‰å­èŠ‚ç‚¹
    childFlags = ChildrenFlags.NO_CHILDREN
  } else if (children._isVNode) {
    // å•ä¸ªå­èŠ‚ç‚¹
    childFlags = ChildrenFlags.SINGLE_VNODE
  } else {
    // å…¶ä»–æƒ…å†µéƒ½ä½œä¸ºæ–‡æœ¬èŠ‚ç‚¹å¤„ç†ï¼Œå³å•ä¸ªå­èŠ‚ç‚¹ï¼Œä¼šè°ƒç”¨ createTextVNode åˆ›å»ºçº¯æ–‡æœ¬ç±»åž‹çš„ VNode
    childFlags = ChildrenFlags.SINGLE_VNODE
    children = createTextVNode(children + '')
  }

  // è¿”å›ž VNode å¯¹è±¡
  return {
    _isVNode: true,
    flags,
    tag,
    data,
    children,
    childFlags,
    el: null
  }
}

function normalizeVNodes(children) {
  const newChildren = []
  // éåŽ† children
  for (let i = 0; i < children.length; i++) {
    const child = children[i]
    if (child.key == null) {
      // å¦‚æžœåŽŸæ¥çš„ VNode æ²¡æœ‰keyï¼Œåˆ™ä½¿ç”¨ç«–çº¿(|)ä¸Žè¯¥VNodeåœ¨æ•°ç»„ä¸­çš„ç´¢å¼•æ‹¼æŽ¥è€Œæˆçš„å­—ç¬¦ä¸²ä½œä¸ºkey
      child.key = '|' + i
    }
    newChildren.push(child)
  }
  // è¿”å›žæ–°çš„childrenï¼Œæ­¤æ—¶ children çš„ç±»åž‹å°±æ˜¯ ChildrenFlags.KEYED_VNODES
  return newChildren
}

export function createTextVNode(text) {
  return {
    _isVNode: true,
    // flags æ˜¯ VNodeFlags.TEXT
    flags: VNodeFlags.TEXT,
    tag: null,
    data: null,
    // çº¯æ–‡æœ¬ç±»åž‹çš„ VNodeï¼Œå…¶ children å±žæ€§å­˜å‚¨çš„æ˜¯ä¸Žä¹‹ç›¸ç¬¦çš„æ–‡æœ¬å†…å®¹
    children: text,
    // æ–‡æœ¬èŠ‚ç‚¹æ²¡æœ‰å­èŠ‚ç‚¹
    childFlags: ChildrenFlags.NO_CHILDREN
  }
}
